package fynotek

class FynotekWord {
    val beginning: String
    val vowels: String
    val end: String
    val isProper: Boolean
    val inflection: Inflection?
    val ablaut
        get() = if (this.toString() == "folo" && inflection == Case.ACCUSATIVE) Ablaut.DEFAULT else inflection?.ablaut
    val isMarked get() = inflection != null

    override fun toString(): String {
        return beginning + vowels + end
    }

    override fun equals(other: Any?): Boolean {
        if (this === other) return true
        if (other == null || this::class != other::class) return false

        other as FynotekWord

        if (beginning != other.beginning) return false
        if (vowels != other.vowels) return false
        if (end != other.end) return false
        if (isProper != other.isProper) return false
        if (inflection != other.inflection) return false

        return true
    }

    override fun hashCode(): Int {
        var result = beginning.hashCode()
        result = 31 * result + vowels.hashCode()
        result = 31 * result + end.hashCode()
        result = 31 * result + isProper.hashCode()
        result = 31 * result + (inflection?.hashCode() ?: 0)
        return result
    }

    operator fun component1() = beginning
    operator fun component2() = vowels
    operator fun component3() = end

    private constructor(beginning: String, vowels: String, end: String, isProper: Boolean = false, inflection: Inflection? = null) {
        this.beginning = beginning
        this.vowels = vowels
        this.end = end
        this.inflection = inflection
        this.isProper = isProper
    }

    private constructor(word: Array<String>, isProper: Boolean = false, inflection: Inflection? = null):
            this(word[0], word[1], word[2], isProper, inflection)

    constructor(word: String, isProper: Boolean = false, inflection: Inflection? = null):
            this(separateVowels(word), isProper, inflection)

    private fun _ablaut(ablaut: Ablaut): Array<String> {
        if (ablaut == Ablaut.DEFAULT || vowels.isEmpty()) return arrayOf(beginning, vowels, end)
        var newVowels = vowels
        if (ablaut === Ablaut.REDUPLICATION) {
            if (newVowels.length == 1 || newVowels[0] != newVowels[1]) {
                newVowels += newVowels.substring(newVowels.length - 1)
                if (newVowels.length > 2) {
                    newVowels = newVowels.substring(newVowels.length - 2)
                }
            } else {
                newVowels = newVowels.substring(0, 1)
            }
        } else {
            if (vowels[vowels.length - 1] != ablaut.asChar) {
                newVowels = newVowels.replace(Regex(".$"), ablaut.asChar.toString())
            } else {
                newVowels += ablaut.secondary
                if (newVowels.length > 2) newVowels = newVowels.substring(newVowels.length - 2)
            }
        }
        return arrayOf(beginning, newVowels, end)
    }

    private fun properSuffix(ablaut: Ablaut): Array<String> {
        if (ablaut === Ablaut.DEFAULT ||
            ablaut === Ablaut.REDUPLICATION && vowels.isEmpty())
            return arrayOf(beginning, vowels, end)
        val suffix = if (ablaut === Ablaut.REDUPLICATION) {
            val vowelToReduplicate = vowels[vowels.lastIndex].toString()
            vowelToReduplicate + vowelToReduplicate
        } else {
            ablaut.asChar.toString()
        }
        val addN = end.isEmpty() && (vowels.length >= 2 || ablaut == Ablaut.REDUPLICATION)
        return arrayOf(beginning + vowels + if (addN) "n" else end, suffix, "")
    }

    fun suffix(suffix: String): FynotekWord {
        if (suffix.isEmpty()) return this
        var output = this.toString()
        output += if (end.isEmpty()) {
            // Check for VVV sequence
            if (suffix[0].isVowel() && (vowels.length >= 2 || (suffix.length >= 2 && suffix[1].isVowel())))
                "n$suffix"
            else suffix
        } else {
            // This is so complex that delegating it to (a subset of) the isValidSequence() function may be needed.
            if (!isValidConsonantSequence(vowels + end + suffix))
                "a$suffix"
            else suffix
        }
        return FynotekWord(output, isProper, inflection)
    }

    fun prefix(prefix: String): FynotekWord {
        return if (prefix.isEmpty()) this
        else FynotekWord(FynotekWord(this.toString().reversed()).suffix(prefix.reversed()).toString(), isProper, inflection)
    }

    fun nounCase(caseOfNoun: Case) = inflect(caseOfNoun)
    fun verbTense(tenseOfVerb: Tense) = inflect(tenseOfVerb)
    fun ablaut(ablaut: Ablaut) = inflect(ablaut)

    fun inflect(inflection: Inflection?): FynotekWord {
        if (inflection == null) return this
        if (this.toString() == "folo" && !isProper) {
            require(inflection != Case.NOMINATIVE) { "\"folo\" cannot be marked for the nominative case" }
            if (inflection == Case.ACCUSATIVE)
                return FynotekWord("fol", "o", "", false, Case.ACCUSATIVE)
        }
        // While there is an actual suffix function, I prefer to leave this simplified ome in for speed.
        return FynotekWord(
            if (isProper) properSuffix(inflection.ablaut) else _ablaut(inflection.ablaut),
            isProper,
            inflection
        )
    }

    fun match(other: FynotekWord) = inflect(other.inflection)

    companion object {
        val FOLO = FynotekWord("fol", "o", "")

        fun separateVowels(word: String): Array<String> {
            val word = word.trim().lowercase()
            // The Kotlin/JS regex tool is not yet powerful enough to do this easily, so we will iterate.
            return if (word.isEmpty()) {
                arrayOf("", "", "")
            } else if (word.length == 1) {
                if (word[0].isVowel()) arrayOf("", word, "")
                else arrayOf("", "", word)
            } else {
                var vowelIndex = 0
                var vowelLength = 0
                for (i in word.length - 1 downTo 0) {
                    if (word[i].isVowel()) {
                        if (i > 0 && word[i - 1].isVowel()) {
                            vowelIndex = i - 1
                            vowelLength = 2
                        } else {
                            vowelIndex = i
                            vowelLength = 1
                        }
                        break
                    }
                }
                arrayOf(
                    word.substring(0, vowelIndex),
                    word.substring(vowelIndex, vowelIndex + vowelLength),
                    word.substring(vowelIndex + vowelLength)
                )
            }
        }

        private fun Char.isVowel() = when (this) {
            'a', 'e', 'i', 'o', 'u', 'y' -> true
            else -> false
        }
        private fun Char.isStop() = when (this) {
            'p', 't', 'k' -> true
            else -> false
        }

        private const val CONSONANTS = "([ptk](?![ptk])|[mn√±rfshjwl])"
        val PHONOTACTICS = Regex("$CONSONANTS{0,2}[aeiouy]{1,2}($CONSONANTS{1,3}[aeiouy]{1,2})*$CONSONANTS{0,2}")

        fun isValidSequence(sequence: String): Boolean {
            val sequence = sequence.trim().lowercase()
            if (sequence.isBlank()) return false

            // Handle a multiple-word sequence
            val wordList = sequence.split("\\s+")
            if (wordList.isEmpty()) return false

            return wordList.all { it.matches(PHONOTACTICS) || it.matches(Regex("an+")) }
        }

        // DO NOT TOUCH. will be reimplemented soon (i hope)
        private fun isValidConsonantSequence(sequence: String): Boolean {
            val sequence = sequence.trim().lowercase()
            // Checks for consonant-related phonotactic problems
            var i = 0
            while (i < sequence.length) {
                val testChar = sequence[i]
                // Consonant check
                var j = i
                while (j < sequence.length) {
                    if (sequence[j].isStop()) break
                    j++
                }
                if (j - i + (if (i == 0 || j == sequence.length) 1 else 0) > 3) return false
                if (j - i == 0) j++

                // Stop+Stop check
                var stopCheck: Boolean = testChar.isStop()
                for (c in sequence.substring(i + 1, j)) {
                    val currentCharIsStop: Boolean = c.isStop()
                    if (stopCheck && currentCharIsStop) return false
                    stopCheck = currentCharIsStop
                }
                i = j
            }
            return true
        }
    }

    enum class Ablaut(val asChar: Char, val secondary: Char) : Inflection {
        DEFAULT('\u0000', '\u0000'),
        A('a', 'e'),
        E('e', 'a'),
        I('i', 'y'),
        Y('y', 'i'),
        O('o', 'u'),
        U('u', 'o'),
        REDUPLICATION('\u0000', '\u0000');

        override val ablaut = this
    }
    enum class Tense(override val ablaut: Ablaut) : SpecificInflection {
        PRESENT(Ablaut.DEFAULT),
        PAST(Ablaut.I),
        FUTURE(Ablaut.O),
        GNOMIC(Ablaut.Y),
        HYP_PRESENT(Ablaut.A),
        HYP_PAST(Ablaut.E),
        HYP_FUTURE(Ablaut.U),
        HYP_GNOMIC(Ablaut.REDUPLICATION)
    }
    enum class Case(override val ablaut: Ablaut) : SpecificInflection {
        NOMINATIVE(Ablaut.DEFAULT),
        ACCUSATIVE(Ablaut.O),
        GENITIVE(Ablaut.I),
        DATIVE(Ablaut.A),
        OTHER(Ablaut.DEFAULT);
    }
}