import fynotek.FynotekWord
import fynotek.FynotekWord.Case
import fynotek.FynotekWord.Tense
import kotlinx.browser.document
import kotlinx.browser.window
import org.w3c.dom.HTMLInputElement

fun main() {
    window.onload = {
        document.getElementById("submit-word")!!.addEventListener("click", {
            val output = getAllConjugations((document.getElementById("enter-word")!! as HTMLInputElement).value,
                (document.getElementById("proper-noun-checkbox")!! as HTMLInputElement).checked)
            if (output.isNotEmpty())
                document.getElementById("output")!!.textContent = output
        })
    }
}

fun getAllConjugations(word: String, isProper: Boolean = false): String {
    if (word.isBlank()) return ""

    // Basically just InflectionDemo
    if (!FynotekWord.isValidSequence(word) || word.matches(Regex("an{3,}")))
        return "This is not a phonotactically valid Fynotek word, so it cannot be conjugated."
    val fynWord = FynotekWord(word, isProper)
    val isFolo = fynWord == FynotekWord.FOLO
    var output = ""

    // You probably should not do this, but oh well
    inline fun println() { output += "\n" }
    inline fun println(o: Any?) { output += o.toString() + "\n" }

    // Noun cases
    println("Noun Cases:")
    println("Nominative:  " + if (isFolo) "N/A" else fynWord.nounCase(Case.NOMINATIVE))
    println("Accusative:  " + fynWord.nounCase(Case.ACCUSATIVE))
    println("Genitive:  " + fynWord.nounCase(Case.GENITIVE))
    println("Dative:  " + fynWord.nounCase(Case.DATIVE))
    println("Other:  " + fynWord.nounCase(Case.OTHER))
    println()

    // Verb tenses or modifier forms, if the word is not a proper noun or "folo"
    val infoString = if (fynWord.isProper || isFolo) "Verb Modifier Forms:" else "Verb Tenses:"
    println("Non-Hypothetical $infoString")
    println("Present:  " + fynWord.verbTense(Tense.PRESENT))
    println("Past:  " + fynWord.verbTense(Tense.PAST))
    println("Future:  " + fynWord.verbTense(Tense.FUTURE))
    println("Gnomic:  " + fynWord.verbTense(Tense.GNOMIC))
    println()
    println("Hypothetical $infoString")
    println("Present:  " + fynWord.verbTense(Tense.HYP_PRESENT))
    println("Past:  " + fynWord.verbTense(Tense.HYP_PAST))
    println("Future:  " + fynWord.verbTense(Tense.HYP_FUTURE))
    println("Gnomic:  " + fynWord.verbTense(Tense.HYP_GNOMIC))

    return output
}